@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
* {
  margin: 0px; padding: 0px;
}


body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  padding: 10px;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 1px 1px #707070;

}

button:hover {
  box-shadow: 0px 0px;
  border: 1px solid white;
}

.log-button {
  padding: 10px;
  border: 1px solid black;
  background-color: rgb(76, 138, 231);
  color: white;
  border-radius: 10px;
}

main {
  width: 90%;
  margin: auto;
}

.red {
  color: red
}

footer {
  text-align: center;
  padding: 5px;
  color: lightgray;
}

footer a {
  color:rgb(163, 189, 228)
}



#login-box {
    margin: 50px auto;
    width: 50%;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    flex-direction: column;
    border: 1px dashed black;
    background-color: white;
    border-radius: 20px;
  }

  #login-box button {
      margin: 10px;
  }

  #login-footer {
    position: absolute;
    bottom: 0px;
  }

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid grey;
  width: 100%;
  height: 60px;
  background: rgb(213, 235, 201);
}

#add-book-button {
  background-color: rgb(231, 231, 231);
  border: 0px;
  box-shadow: 1px 1px #707070;
}

#add-book-button:hover {
  background-color: rgb(212, 212, 212);
  border: 0px;
  box-shadow: 1px 1px #e6dbdb;
}


.header-wrapper {
  display: flex;
  align-items: center;
}

#header-items-left img {
  width: 50px;
  height: 50px;
  margin: 4px;
}

header button {
  margin: 10px;
}


.header-text {
  padding-right: 20px;
}


#hamburger-toggle input {
  display: none;
}

#hamburger-toggle input:checked + #menu {
  display: block;
  position: absolute;
  z-index: 1;
  background-color: white;
  border: 1px solid black;
}

.toggle-item {
  display: block;
  width: 30px;
  border-top: 3px solid black;
  padding-bottom: 4px;
  border-radius: 10%;
}



#menu {
  display: none;
}

#menu ul {
  list-style-type: none;
  padding: 0px;
margin: 0px;
}

#menu li {
  padding: 5px 15px;
}

#menu li:not(:first-child) {
  border-top: 1px solid grey;
}

#menu li:hover {
  background-color: grey;
}


#search-box {
  display: inline;
}
#search {
  margin-top: 5px;
}
#full-library-table tr:nth-child(odd){
    background: rgb(213, 235, 201);
}

h1 {
    padding: 0px; margin: 0px;
}
#add-book-form {
    margin-top: 10px;
    
}
